import React, {
  Fragment,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";

// CSS  ////
import "../../css/glist.scss";

//   /////
import gdata from "../../js/data/glist-items";

import { pCon } from "../modules/pCon";
import ItemDetail from "../modules/ItemDetail";

//   ///
import $ from "jquery";

function GList() {
  // [1]     ////////////////////////

  // 1.   : 
  const [pgNum, setPgNum] = useState(1);

  // 2.    : men/women/style  
  const [chkSts, setChkSts] = useState([true, true, true]);

  // 3.    :  
  const [force, setForce] = useState(false);

  // 4.    : more   
  const [moreCnt, setMoreCnt] = useState(1);

  // [2]    :  ! ////////////

  // 1.  (m1,m2,m3,...)
  const item = useRef("m1");

  // 2. (men/women/style)
  const catName = useRef("men");

  // 3.   
  const pgSize = useRef(10);

  // 4.   
  const pgCnt = useRef(Math.ceil(gdata.length / pgSize.current));
  // /  ->  Math.ceil()
  //   1    !

  // 5.   
  const moreSize = useRef(5);

  // 6.   
  const moreLimit = useRef(Math.ceil(gdata.length / moreSize.current));
  // /  ->  Math.ceil()

  console.log("", gdata.length);
  console.log(" ", pgSize.current);
  console.log("  ", pgCnt.current);

  //  API 
  const myCon = useContext(pCon);

  /* 
        [    ]

        {
            idx: "1",
            cat: "men",
            ginfo: [
            "m1", 
            "[] PQ ", 
            "DMTS7K731-MG", 
            "99000"],
        }
    */
  // [   ] /////////
  //    
  const selData =
    //       ///
    //  [1] 'F' : men/women/style  
    myCon.gMode === "F"
      ? gdata.filter(
          (v) =>
            v.cat === (chkSts[0] ? "men" : "") ||
            v.cat === (chkSts[1] ? "women" : "") ||
            v.cat === (chkSts[2] ? "style" : "")
        )
      : //  [2] 'P' :   
      // ->      
      // ->  = ( - 1) *  
      // ->  =  *  
      myCon.gMode === "P"
      ? gdata.slice((pgNum - 1) * pgSize.current, pgNum * pgSize.current)
      : //  [3] 'M' :   
      // ->   : ( * ) 
      // ->  *  : 5*1,5*2,...)
      // -> 5, 10, 15,...
      myCon.gMode === "M"
      ? gdata.slice(0, moreCnt * moreSize.current)
      : //      
        [];

  console.log(":", selData);

  // [    ] //////
  const makeCode = () => {
    // 
    let retVal;

    // [   : selData!!! ] /////////
    retVal =
      //    /////////
      selData.length === 0 ? (
        <div style={{ margin: "0 auto" }}>
          <p></p>
        </div>
      ) : (
        //   ////////
        selData.map((v, i) => (
          <div key={i}>
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                //   
                showDetail(v.ginfo[0], v.cat);
                // showDetail(, );
              }}
            >
              {/* 1,2,3,4   */}[{i + 1}]
              <img
                src={
                  process.env.PUBLIC_URL +
                  "/images/goods/" +
                  v.cat +
                  "/" +
                  v.ginfo[0] +
                  ".png"
                }
                alt={v.ginfo[1]}
              />
              <aside>
                <h2>{v.ginfo[1]}</h2>
                <h3>{myCon.addCommas(v.ginfo[3])}</h3>
              </aside>
            </a>
          </div>
        ))
      );

    //  ////
    return retVal;
  }; ////////////// makeCode  //////////

  // [   ] ////
  const showDetail = (gCode, catCode) => {
    // gCode - , catCode - 
    console.log("!", gCode, catCode);

    // 1. 
    item.current = gCode;
    catName.current = catCode;

    // 2.  
    setForce(!force);
    // ->     !
    // -> ItemDetail   !

    //  
    $(".bgbx").slideDown(600);
  }; //////////// showDetail  ///////////

  //   ////////
  useEffect(() => {
    //   !
    window.scrollTo(0, 0);
  }, [myCon.gMode]);
  //  gMode !

  //    /////
  useEffect(() => {
    //   li on
    $(".gnb li")
    .first().addClass("on")
    .siblings().removeClass("on") ;


    //   class 'on' 
    $(".gnb li")
    .on('click',(e) => {
      $(e.currentTarget).addClass("on")
      .siblings().removeClass("on");
    });

    //   gMode 'F'  'F' 
    if (myCon.gMode !== "F") {
      myCon.setGMode("F");
    }

    //     
    $("#logo a").on("click", (e) => {
      e.preventDefault();
      //  : !
      myCon.goPage("/");
    }); //////// click ////////

    //    ///
    return(()=>{
      $(".gnb li").off("click");
      $("#logo a").off("click");
    });
  }, []); ////    //////

  //  /////////////////
  //   ///////////////////////////
  //  /////////////////
  return (
    <main id="cont">
      <h1 className="tit">All ITEMS List</h1>
      {
        // 1. Filter List 
        myCon.gMode === "F" && (
          <section>
            {/*   */}
            <div id="optbx">
              <label htmlFor="men"></label>
              <input
                type="checkbox"
                className="chkbx"
                id="men"
                checked={chkSts[0]}
                onChange={(e) => {
                  //   
                  setChkSts([e.target.checked, chkSts[1], chkSts[2]]);
                  console.log(e.target.checked);
                }}
              />
              <label htmlFor="women"></label>
              <input
                type="checkbox"
                className="chkbx"
                id="women"
                checked={chkSts[1]}
                onChange={(e) => {
                  //   
                  setChkSts([chkSts[0], e.target.checked, chkSts[2]]);
                  console.log(e.target.checked);
                }}
              />
              <label htmlFor="style"></label>
              <input
                type="checkbox"
                className="chkbx"
                id="style"
                checked={chkSts[2]}
                onChange={(e) => {
                  //   
                  setChkSts([chkSts[0], chkSts[1], e.target.checked]);
                  console.log(e.target.checked);
                }}
              />
            </div>
            {/*    */}
            <div
              className="grid"
              style={{
                gridTemplateColumns:
                  selData.length === 0 ? "repeat(1, 1fr)" : "",
              }}
            >
              {makeCode()}
            </div>
          </section>
        )
      }
      {
        // 2. Paging List 
        myCon.gMode === "P" && (
          <section>
            {/*    */}
            <div
              className="grid"
              style={{
                gridTemplateColumns:
                  selData.length === 0 ? "repeat(1, 1fr)" : "",
              }}
            >
              {makeCode()}
            </div>
            {/*   */}
            <div id="paging">
              {
                // [   ]
                // [1] Array.from({length:})  
                // [2] [...Array()]  
                // [3] Array().fill(0)  
                // ->     map((_,i)=>{})
                //  (_)     
                // -> Array().map()   map 
                // ->       map 
                // -> [...Array()]  Array().fill(0) 
                // -> fill(0)   0  !
                // Array.from({ length: pgCnt.current }).map((v, i) => (
                [...Array(pgCnt.current)].map((_, i) => (
                  <Fragment key={i}>
                    {
                      //  
                      pgNum === i + 1 ? (
                        //    b 
                        <b>{i + 1}</b>
                      ) : (
                        //  a 
                        <a
                          href="#"
                          onClick={(e) => {
                            // 
                            e.preventDefault();
                            //  
                            setPgNum(i + 1);
                          }}
                        >
                          {i + 1}
                        </a>
                      )
                    }
                    {
                      //    
                      i + 1 < pgCnt.current && <span> | </span>
                    }
                  </Fragment>
                ))
              }
            </div>
          </section>
        )
      }
      {
        // 3. More List 
        myCon.gMode === "M" && (
          <section>
            {/*    */}
            <div
              className="grid"
              style={{
                gridTemplateColumns:
                  selData.length === 0 ? "repeat(1, 1fr)" : "",
              }}
            >
              {makeCode()}
            </div>
            {/*   */}
            <div
              id="more"
              style={{
                //     
                display: moreCnt === moreLimit.current ? "none" : "block",
              }}
            >
              <button
                className="more"
                onClick={(e) => {
                  //   1 
                  setMoreCnt(moreCnt + 1);
                }}
              >
                MORE
              </button>
            </div>
          </section>
        )
      }

      {/* 4.  */}
      <div
        className="bgbx"
        style={{
          position: "fixed",
          top: 0,
          paddingTop: "12vh",
          backdropFilter: "blur(8px)",
          height: "100vh",
          zIndex: 9999,
        }}
      >
        {/*    */}
        <ItemDetail catName={catName.current} goods={item.current} />
      </div>
    </main>
  );
}

export default GList;
