// Pilot PJ    

//  CSS 
import "../../css/banner.scss";

export function Banner() {
  //  
  const BAN_CNT = 6;

  //    ////
  const makeList = (gubun) => {
    // gubun : true -  / false - 

    // )
    // 0/1 false/true  
    //    !  true/false
    //     !

    // for     !
    // ->   JSX   
    // for      !
    let hcode = [];

    for (let i = 0; i < BAN_CNT; i++) {
      if (gubun) {
        // 
        hcode[i] = (
          <li className={"ban" + (i == 0 ? "6" : i)} key={i}>
            <span className="ir">{i == 0 ? "6" : i}</span>
          </li>
        );
      } else {
        // 
        hcode[i] = (
          <li className={i == 0 ? "on" : ""} key={i}>
            <a href="#">
              <span className="bld"></span>
            </a>
          </li>
        );
      }
    } //////// for //////////

    // console.log(hcode);

    // 
    return hcode;
  }; ///////// makeList  /////////

  return (
    <>
      {/*      JSX 
    JSX      !
    map()       ! */}
      <ul className="slide">{makeList(true)}</ul>
      <ol className="bindic">{makeList(false)}</ol>
      <div className="cover"></div>
    </>
  );
} //////////// Banner  ///////////
