import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import "./css/swiper_ban.scss";

// import required modules
//    !
// ( , ,)
import { Navigation, Pagination, Autoplay } from "swiper/modules";

export default function SwiperBan({ catName }) {
  // catName -  

  //   ////
  const makeList = (num) => {
    // num -  
    let temp = [];
    for (let x = 0; x < num; x++) {
      temp[x] = (
        <SwiperSlide key={x}>
          {
            //     
            catName == "men" && x == 2 ? (
              <video
                src={process.env.PUBLIC_URL +"/images/sub/" + catName + "/banner/cgv.mp4"}
                style={{ width: "100%", height: "100%", objectFit: "cover" }}
                muted
                loop
                autoPlay
              />
            ) : (
              <img
                src={process.env.PUBLIC_URL + 
                  "/images/sub/" + catName + "/banner/ban" + (x + 1) + ".png"
                }
              />
            )
          }
        </SwiperSlide>
      );
    } /////////// for /////////////

    //  
    return temp;
  }; ///////////// makeList  //////////

  //   /////////
  return (
    <>
      <Swiper
        //   
        slidesPerView={1}
        //   ( px)
        spaceBetween={0}
        //   (import!)
        navigation={true}
        //   (import!)
        pagination={{
          clickable: true, //
        }}
        //    
        autoplay={{
          delay: 3000, //  3
          disableOnInteraction: false, //   false
          // ->      !
        }}
        //     ( !)
        modules={[Navigation, Pagination, Autoplay]}
        //  
        loop={true}
        className="mySwiper"
      >
        {
          //    
          makeList(catName === "style" ? 5 : 3)
        }
      </Swiper>
    </>
  );
}
