// Pilot PJ    - Main.jsx

import React, { useContext, useEffect } from "react";

//   JS  ///
import * as autoFn from "../../js/func/jquery-autoScroll";

//   JS  ////
import { dragBanner } from "../../js/func/drag_banner";

import { Banner } from "../modules/Banner";
import { FashionIntro } from "../modules/FashionIntro";

//   //////
import $ from "jquery";
import { pCon } from "../modules/pCon";

function Main() {
  //  API  ////
  const myCon = useContext(pCon);


  //    :  ([]) /////
  useEffect(() => {   
    //   
    //     !
    myCon.setCatName('main');

    //   ///
    $('html,body').css({overflow:'hidden'});

    
    //   
    window.scrollTo(0,0);

    //   
    window.addEventListener("wheel", autoFn.wheelFn);
    // -> window    ?
    // ->>>      
    //     !

    // +     ///
    autoFn.evtFn();

    //   
    autoFn.initSet();

    //    
    autoFn.zeroPno();

    //   
    dragBanner();

    //    ////////
    return () => {
      console.log("!!!");

      //   
      window.removeEventListener("wheel", autoFn.wheelFn);
      // ->   window  
      //         !

      //    
      autoFn.removeEvtFn();
    }; //////    ///////////
  }, []); ///////// useEffect :  //////////////

  //   //////////////
  return (
    <>
      {/* 1.   */}
      <section
        id="ban"
        className="page none-sel"
        style={{ background: "lightblue" }}
      >
        {/*  */}
        <Banner />
      </section>

      {/* 2.   */}
      <section className="page">
        <FashionIntro cat="men" subcat="etc" />
      </section>

      {/* 3.   */}
      <section className="page">
        <FashionIntro cat="women" subcat="etc" />
      </section>

      {/* 4.   */}
      <section className="page">
        <FashionIntro cat="style" subcat="etc" />
      </section>

      {/*     */}
      <nav className="indic">
        <ul>
          <li className="on">
            <a href="#ban">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#men">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#women">
              <span></span>
            </a>
          </li>
          <li>
            <a href="#style">
              <span></span>
            </a>
          </li>
        </ul>
      </nav>
    </>
  );
}

export default Main;
