// Pilot PJ -   ////////


//    
import { fsData } from "../../js/data/fashion_intro";

//   CSS 
import "../../css/fashion_intro.scss";
import { Link } from "react-router-dom";

//  API 
import { useContext } from "react";
import { pCon } from "./pCon";

export function FashionIntro({ cat, subcat }) {
  // cat -  
  // subcat -  

  //  
  const myCon = useContext(pCon);
  // pCon Provider value   / !

  // 
  let selData = fsData[cat];

  //       
  if (subcat !== "etc") {
    // selData  : cat  'sub'
    // subcat    : [,]
    selData = fsData[cat][subcat[0]][subcat[1]];
    //  fs['sub']['men'][0]
  } //////// if ////////////

  //  
  const newStyle = {};
  // 'women'   
  if (cat == "women") newStyle.flexDirection = "row-reverse";

  //   ////////////////////////////
  return (
    <div id={cat} className="fs-page">
      <ul
        className="pgc"
        style={newStyle}
        //   style={cat=='women'?{flexDirection:'row-reverse'}:{}}
      >
        {/*   ! */}
        {
          //   subcat etc  !
          subcat === "etc" && (
            <li className="imgc">
              <img
                src={process.env.PUBLIC_URL + (cat == "style" ? selData.isrc[0] : selData.isrc)}
                alt={cat == "style" ? selData.ialt[0] : selData.ialt}
              />
            </li>
          )
        }
        {/*  2,  1 */}
        {subcat === "etc" && (
          <li className="txtc">
            {cat != "style" && (
              <h2 onClick={()=>myCon.setCatName(cat)}>
                <Link to="/fashion" state={{ catName: cat }}>
                  {selData.tit[0]} <br />
                  {selData.tit[1]}
                </Link>
              </h2>
            )}
            {cat == "style" && (
              <>
                <h2 className="tm" 
                onClick={()=>myCon.setCatName(cat)}>
                  <Link to="/fashion" state={{ catName: cat }}>
                    {selData.tit[0][0]} <br />
                    {selData.tit[0][1]}
                  </Link>
                </h2>
                <h2 className="tw" 
                onClick={()=>myCon.setCatName(cat)}>
                  <Link to="/fashion" state={{ catName: cat }}>
                    {selData.tit[1][0]} <br />
                    {selData.tit[1][1]}
                  </Link>
                </h2>
              </>
            )}
          </li>
        )}
        {/*     */}
        {cat == "style" && (
          <li className="imgc">
            <img src={process.env.PUBLIC_URL + selData.isrc[1]} alt={selData.ialt[1]} />
          </li>
        )}

        {/*   */}
        {
          // sub  sub   !
          cat == "sub" && subcat[1] === 0 && (
            <>
              {/*  */}
              <li className="txtc">
                <h2 className="sc-ani">
                  <a href="#">
                    {selData.tit[0]} <br />
                    {selData.tit[1]}
                  </a>
                </h2>
              </li>
              {/*  */}
              <li className="imgc sc-ani">
                <img src={process.env.PUBLIC_URL + selData.isrc} alt={selData.ialt} />
              </li>
            </>
          )
        }
        {
          // sub  sub   !
          cat == "sub" && subcat[1] === 1 && (
            <>
              {/*  */}
              <li className="imgc sc-ani">
                <img src={process.env.PUBLIC_URL + selData.isrc[0]} alt={selData.ialt[0]} />
              </li>
              {/*  */}
              <li className="txtc">
                <h2 className="tm sc-ani">
                  <a href="#">
                    {selData.tit[0][0]} <br />
                    {selData.tit[0][1]}
                  </a>
                </h2>
                <h2 className="tw sc-ani">
                  <a href="#">
                    {selData.tit[1][0]} <br />
                    {selData.tit[1][1]}
                  </a>
                </h2>
              </li>
              {/*  */}
              <li className="imgc sc-ani">
                <img src={process.env.PUBLIC_URL + selData.isrc[1]} alt={selData.ialt[1]} />
              </li>
            </>
          )
        }
      </ul>
    </div>
  );
} //////////// FashionIntro  ////////
