//     JS : jquery-autoScroll.js

//   ! /////

//  
import $ from "jquery";
window.jQuery = $;
require("jquery-ui-dist/jquery-ui");
require("jquery-ui-touch-punch/jquery.ui.touch-punch");

// export function autoScroll() {
/****************************************** 
     
  ******************************************/
//  
let pno = 0;
//   
const zeroPno = () => {
  pno = 0;
};
//  
let pg;
//  
let pgcnt;
// console.log(":", pgcnt, pg);
// 
let prot = [];
// 
prot[0] = 0;

//     
$(() => {
  //  
  pg = $(".page");
  //  
  pgcnt = pg.length;
}); /////////// load ///////////////

/****************************************** 
     
    ->>>   
         
       
     ! -> ?
     JS   !
    ?   
        
        
    ! 
  ******************************************/
//   
// $(window).on("wheel", wheelFn); ->  X
// window.addEventListener('wheel',wheelFn);

//    (!)
$("html,body").animate({ scrollTop: "0px" });

/**************************************** 
    : wheelFn
    :    
    ->   
    ****************************************/
function wheelFn(e) {
  // 
  if (prot[0]) return;
  chkCrazy(0);

  // console.log("~~~~~~!");

  // 1. 
  let delta = e.wheelDelta;
  // console.log(delta);

  // 2.    
  if (delta < 0) {
    pno++;
    if (pno === pgcnt) pno = pgcnt - 1;
    //   !
  } //// if /////
  else {
    pno--;
    if (pno === -1) pno = 0;
    //  !
  } //// else ////

  // console.log(pno);

  // 3.   +  "on"
  movePg();
} /////////////// wheelFn  ///////////////

/******************************************** 
    : chkCrazy
    :   
    ********************************************/
function chkCrazy(seq) {
  // seq  
  prot[seq] = 1;
  setTimeout(() => (prot[seq] = 0), 800);
} //////// chkCrazy //////////////

/******************************************** 
    : movePg
    :  
    ********************************************/
function movePg() {
  // : html,body ->     !
  $("html,body")
    .stop()
    .animate(
      {
        scrollTop: $(window).height() * pno + "px",
      },
      700,
      "easeInOutQuint",
      actPage
      //   actPage !
    ); ///// animate //////

  //   on 
  addOn();
} ///////////////// movePg ////////////////

/////////////////////////////////////////////////////
// GNB +     'on' //
/////////////////////////////////////////////////////
//  +      !
const addOn = () => {
  //   class 'on' 
  $(".gnb li").eq(pno).addClass("on").siblings().removeClass("on");

  $(".indic li").eq(pno).addClass("on").siblings().removeClass("on");
}; //////////// addOn ////////////

/******************************************** 
        [     ]
        1.   : () 
        2.   :   
            (1) .page .imgc - 
            (2) .page .txtc h2 a - 
        3.  :
            [   ]
            (())
            transform: rotate(45deg);
            opacity: 0;
            transition: 1s 1s; ->  
            (())
            transform-origin: left top;
            display: inline-block; -> a
    ********************************************/

/*************************************** 
     : initSet
     :   
  ***************************************/
function initSet() {
  // 1. 

  // :  - .imgc
  $(".imgc").css({
    transform: "rotate(45deg)",
    transformOrigin: "-10% -10%",
    opacity: 0,
    transition: "1s ease-in-out",
  }); /////////// css //////////

  // :  - .txtc a
  $(".txtc a").css({
    transform: "rotate(45deg)",
    transformOrigin: "-100% -100%",
    opacity: 0,
    transition: "1s ease-in-out .5s",
    display: "inline-block",
  }); /////////// css //////////
} /////////// initSet  ///////////////

/***************************************** 
  : actPage
  :    
 *****************************************/
function actPage() {
  console.log("~!!!", pno);

  // pno 0  4  !
  if (pno != 0 || pno != 4) {
    // :  .page  .imgc  .txtc a
    $(".page").eq(pno).find(".imgc,.txtc a").css({
      transform: "rotate(0deg)",
      opacity: 1,
    }); ///////// css /////////
  } ///////// if //////////////

  //   !
  if (pno == 0) initSet();
} ///////// actPage  //////////////////

//   ///////
function evtFn() {
  /////////////////////////////////////////////
  // GNB  +     //
  /////////////////////////////////////////////
  $(".gnb li, .indic li").on("click", function () {
    // 1. 
    let idx = $(this).index();
    // console.log('~!',idx);

    // 2.   (!)
    pno = idx;

    // 3.  
    movePg();
  }); ///// click //////////

  //   
  // 1. Page Up(33) / Up Arrow (38)
  // 2. Page Down(34) / Down Arrow (40)
  $(document).on("keydown", (e) => {
    // 
    if (prot[0]) return;
    chkCrazy(0);

    // 
    if (e.keyCode === 33 || e.keyCode === 38) {
      pno--;
      if (pno === -1) pno = 0;
      movePg();
    }
    // 
    else if (e.keyCode === 34 || e.keyCode === 40) {
      pno++;
      if (pno === pgcnt) pno = pgcnt - 1;
      movePg();
    }
  }); ///////////// keydown ////////////////

  //      !
  $("#logo a").on("click", (e) => {
    e.preventDefault();
    pno = 0;
    movePg();
  }); //////// click ////////
} /////////// evtFn  /////////////////

// [   ] /////////////
function removeEvtFn() {
  // off() ->     
  $("#logo a").off("click");
  $(".gnb li, .indic li").off("click");
  $(document).off("keydown");
} ///////// removeEvtFn  /////////////

//   !
export { wheelFn, evtFn, removeEvtFn, initSet, zeroPno };

// } ///////////// autoScroll  //////////
