// Pilot PJ   

//   CSS 
import { Fragment, memo, useEffect, useState } from "react";
import "../../css/cartlist.scss";

// 
import $ from "jquery";

//      !
export const CartList = memo(({ selData, flag }) => {
  // selData -   
  // flag -  (true/false) ->  !
  console.log(":", flag.current);

  // [  ] /////////////
  // 1.   :    
  const pgBlock = 5;

  // console.log(":", pgBlock, "\n :", totNum);

  //   ///////////////
  // 1.    :    !
  const [pgNum, setPgNum] = useState(1);
  // 2.    :   
  const [cartData, setCartData] = useState(selData);
  // 3.   
  const [force, setForce] = useState(null);

  console.log(" ", selData, "\n ", cartData);

  //     
  //     
  //      !
  //   ??? ->  !!!
  //  flag.current true !
  if (cartData !== selData && flag.current) {
    setCartData(selData);
    console.log(3333);
  }

  //   :   ! -> 
  // const selData = JSON.parse(localStorage.getItem("cart"));

  //  
  const cntData = cartData.length;

  console.log(cartData, cntData + "");

  //  
  let totalCnt = 0;
  cartData.forEach((v) => {
    totalCnt += v.ginfo[3] * v.num;
  }); ////////// forEach ///////////

  console.log(":", totalCnt);

  //(   )
  function addComma(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  //   /////////////
  useEffect(() => {
    //  
    $("#mycart")
      .removeClass("on")
      .fadeIn(300, function () {
        //  
        $(this).addClass("on");
      }); ////// fadeIn ////////

    console.log("");
  }, []); /// useEffect ///////////////

  //    //////////
  const showList = () => {
    console.log("!!");
    $("#cartlist").animate({ right: "0" }, 600);
  }; //////////// showList ////////////

  //    //////////
  const hideList = (e) => {
    e.preventDefault();
    $("#cartlist").animate({ right: "-60%" }, 600);
  }; //////////// hideList ////////////

  //    ////////////////
  const deleteItem = (e) => {
    //      useRef flag
    // false     !
    //  !
    flag.current = false;

    let confMsg = " ? ?";
    //    !
    // confirm() 
    // ''->true, ''->false !
    // confirm alert  window !
    if (window.confirm(confMsg)) {
      const selIdx = $(e.target).attr("data-idx");
      console.log(":", selIdx);

      //    
      const newData = cartData.filter((v) => {
        if (v.idx !== selIdx) return true;
      });

      console.log(":", newData);

      //   !!!
      localStorage.setItem("cart", JSON.stringify(newData));

      //       !
      setCartData(newData);
    } ////// if /////////
  }; ////////// deleteItem  //////////

  //   ////////////
  const chgNum = (e) => {
    //  
    const tg = $(e.currentTarget);
    //   
    const tgInput = tg.parent().siblings(".item-cnt");
    //    : ->
    let cNum = Number(tgInput.val());

    console.log(":");

    // CSS     
    //    !
    tgInput.focus();

    // 
    if (tg.attr("alt") === "") cNum++;
    else cNum--;

    // 
    if (cNum < 1) cNum = 1;

    // 
    tgInput.val(cNum);
  }; ///////// chgNum  ///////////

  //     ////
  const goResult = (e) => {
    //    idx
    let tg = $(e.currentTarget);
    let cidx = tg.attr("data-idx");
    console.log(" ~!", cidx);

    //   
    flag.current = false;

    //    
    // forEach     return  !
    //  for  return  continue  

    // ->>> some()   !!!
    // return true  
    // for (return )
    // return false   for 
    //   (continue !)
    // : https://www.w3schools.com/jsref/jsref_some.asp

    // [Array some()  ] //////
    // cartData.some((v) => {
    //   console.log('some:',v.idx);
    //   // if(v.idx==17){return true;} // -> for break 
    //   if(v.idx==17){return false;} // -> for continue 
    //   console.log('some:',v.idx);
    // });

    //  'data-idx'   idx !->cidx
    cartData.some((v, i) => {
      //  
      if (v.idx == cidx) {
        //   ///
        cartData[i].num = tg.prev().val();

        // some   true  !
        return true;
      } ///// if ///////
    });

    //   !!!
    localStorage.setItem("cart", JSON.stringify(cartData));

    //       !
    setCartData(cartData);
    // .....   /  
    //     !
    //       
    //   !
    setForce(Math.random());
    //     !!!^^
  }; ////////// goResult  //////////

  /************************************* 
     : bindList
     :    
  *************************************/
  const bindList = () => {
    //  
    const tempData = [];

    //  : (-1)*
    let initNum = (pgNum - 1) * pgBlock;
    //  : *
    let limitNum = pgBlock * pgNum;

    //   for : (orgData) 
    for (let i = initNum; i < limitNum; i++) {
      //   (cntData)
      if (i >= cntData) break;
      //  
      tempData.push(cartData[i]);
    } ///// for /////

    //     ///
    if (cartData.length === 0) {
      return (
        <tr>
          <td colSpan="8">There is no data.</td>
        </tr>
      );
    } ////// if /////////

    // console.log('Data:',tempData);

    return tempData.map((v, i) => (
      <tr key={i} data-v={v}>
        {/*  */}
        <td>
          <img
            src={
              process.env.PUBLIC_URL +
              "/images/goods/" +
              v.cat +
              "/" +
              v.ginfo[0] +
              ".png"
            }
            alt="item"
          />
        </td>
        {/*  :   */}
        <td>{i + 1 + initNum}</td>
        {/*  */}
        <td>{v.ginfo[1]}</td>
        {/*  */}
        <td>{v.ginfo[2]}</td>
        {/*  */}
        <td>{addComma(v.ginfo[3])}</td>
        {/*  */}
        <td className="cnt-part">
          <div>
            <span>
              <input type="text" className="item-cnt" readOnly value={v.num} />
              {/*    value!
              defaultValue   ! */}
              <button
                className="btn-insert"
                onClick={goResult}
                data-idx={v.idx}
              >
                
              </button>
              <b className="btn-cnt">
                <img src={process.env.PUBLIC_URL + "/images/cnt_up.png"} alt="" onClick={chgNum} />
                <img src={process.env.PUBLIC_URL + "/images/cnt_down.png"} alt="" onClick={chgNum} />
              </b>
            </span>
          </div>
        </td>
        {/*   */}
        <td>{addComma(v.ginfo[3] * v.num)}</td>
        {/*  */}
        <td>
          <button className="cfn" data-idx={v.idx} onClick={deleteItem}>
            
          </button>
        </td>
      </tr>
    ));
  }; /////////// bindList  ////////////

  /************************************* 
     : pagingLink
     :    !
  *************************************/
  const pagingLink = () => {
    //   ////
    // 1.  
    const blockCnt = Math.floor(cntData / pgBlock);
    //  /  (  +1)
    //  : pgBlock !
    // 2.  
    const blockPad = cntData % pgBlock;

    //   ->    1
    const limit = blockCnt + (blockPad === 0 ? 0 : 1);

    // console.log(
    //   ":",
    //   blockCnt,
    //   "\n:",
    //   blockPad,
    //   "\n:",
    //   limit
    // );

    //  jsx   
    //    !!!
    let pgCode = [];
    //   //////////
    //    key  
    //  Fragment   !
    for (let i = 0; i < limit; i++) {
      pgCode[i] = (
        <Fragment key={i}>
          {pgNum - 1 === i ? (
            <b>{i + 1}</b>
          ) : (
            <a href="#" onClick={chgList}>
              {i + 1}
            </a>
          )}

          {i < limit - 1 ? " | " : ""}
        </Fragment>
      );
    } ////// for /////

    return pgCode;
  }; /////////// pagingLink  ////////

  /************************************* 
     : chgList
     :    
  *************************************/
  const chgList = (e) => {
    //  !!! ////
    e.preventDefault();
    //    
    let currNum = e.target.innerText;
    // console.log(":", currNum);
    //   ! ->  !
    setPgNum(currNum);
    //    !!!
    // ? pgNum bindList() 
    //   !!!
  }; ///////// chgList  //////////////

  ///   ///////////////////////
  return (
    <>
      <section id="cartlist">
        <a href="#" className="cbtn cbtn2" onClick={hideList}>
          <span></span>
        </a>
        <table>
          <caption>
            <h1>  </h1>
          </caption>
          <tbody>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>

            {bindList()}

            <tr>
              <td colSpan="6"> :</td>
              <td>{addComma(totalCnt)}</td>
              <td></td>
            </tr>
          </tbody>
          {/*    */}
          <tfoot>
            <tr>
              <td colSpan="8" className="paging">
                {/*    */}
                {pagingLink()}
              </td>
            </tr>
          </tfoot>
        </table>
      </section>
      {/*   */}
      <div id="mycart" onClick={showList}>
        {/*  */}
        <img
          src={process.env.PUBLIC_URL + "/images/mycart.gif"}
          title={cntData + "  "}
        />
        {/*   */}
        <div className="cntBx">{cntData}</div>
      </div>
    </>
  );
}); ////////////// CartList  /////////
