// Pilot PJ :    - ItemDetail.jsx

import React, { useContext, useEffect } from "react";

//   
import gdata from "../../js/data/glist-items";

//  API 
import { pCon } from "./pCon";

//  ////
import $ from "jquery";

function ItemDetail({ catName, goods }) {
  // catname - (men/women/style)
  // goods -  (: m1,m2,...)

  //  API  /////
  const myCon = useContext(pCon);

  //////////////////////////////////////
  //      ////
  /////////////////////////////////////
  const useCart = (e) => {
    e.stopPropagation();
    //     
    //    useRef  flag
    // true  !!!
    myCon.flag.current = true;

    // 1.   !
    /*  :
    {
      idx: ,
      cat: ,
      ginfo: ,
      num: 
    }
    ->   selData 
    ->  num !
    */
    // 1-1.num  :  #sum value
    selData.num = $("#sum").val();

    console.log("~!", selData);

    //    
    let localD;

    // 1-2.  
    // (1)     
    if (!localStorage.getItem("cart")) {
      //      push!
      localD = [];
      localD.push(selData);
      localStorage.setItem("cart", JSON.stringify(localD));

      // localD   
      // transData 
      // CartList  !
      myCon.setTransData(localD);

      console.log(myCon.transData);

      myCon.setCsts(1); // 1 true !

      //  
      $("#mycart")
        .removeClass("on")
        .delay(1000)
        .fadeIn(300, function () {
          //  
          $(this).addClass("on");
        }); ////// fadeIn ////////
    } ///// if //////
    // (2)       
    else {
      localD = localStorage.getItem("cart");
      // 
      localD = JSON.parse(localD);
      // console.log(':',localD,selData);

      // ****     
      //    !
      let temp = localD.find((v) => {
        if (v.idx === selData.idx) return true;
      }); ////// find /////

      console.log("?", temp);

      //      
      if (temp) {
        alert("  !");
      } /////// if //////

      // ****   ! **** ///
      else {
        //   push !
        localD.push(selData);
        // //   
        localStorage.setItem("cart", JSON.stringify(localD));

        // localD   
        // transData 
        // CartList  !
        myCon.setTransData(localD);

        console.log(myCon.transData);

        myCon.setCsts(1);

        //  
        $("#mycart")
          .removeClass("on")
          .delay(1000)
          .fadeIn(300, function () {
            //  
            $(this).addClass("on");
          }); ////// fadeIn ////////
      } ///////// else //////////
    } //////////// else /////////////
  }; /////////// useCart ////////////

  //  : [][].split('^')
  // ->   !
  // [,,]
  // const selData = sinsangData[catName][goods].split('^');
  // console.log(':',selData);

  const selData = gdata.find((v) => {
    // :    
    if (v.cat === catName && v.ginfo[0] === goods) return true;
  });
  // filter   
  // find   
  //    find !

  console.log(":", selData,catName,gdata,goods);

  // selData     
  //     !!!

  //     ginfo!
  const ginfo = selData.ginfo;

  //   ////
  const closeBox = (e) => {
    e.preventDefault();
    $(".bgbx").slideUp(400);
  };

  //   ///////
  useEffect(() => {
    //  input
    const sum = $("#sum");
    //  
    const numBtn = $(".chg_num img");

    //    /////////
    numBtn.click((e) => {
      // 
      let seq = $(e.currentTarget).index();
      //  
      let num = Number(sum.val());
      //  ++,  --
      seq ? num-- : num++;
      // 
      if (num < 1) num = 1;
      console.log(":", seq, num);
      //  
      sum.val(num);
      //  
      //  : selData[2]
      //  : #total
      $("#total").text(addComma(ginfo[3] * num) + "");
    });
  }, []); ////    /////

  //   /////
  useEffect(() => {
    // 
    $("#sum").val("1");
    // 
    $("#total").text(addComma(ginfo[3]) + "");
  }); ////////// useEffect //////

  //(   )
  function addComma(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  //   ////////////////
  return (
    <>
      <a href="#" className="cbtn" onClick={closeBox}>
        <span className="ir"></span>
      </a>
      <div id="imbx">
        <div className="inx">
          <section className="gimg">
            <img
              src={process.env.PUBLIC_URL + "/images/goods/" + catName + "/" + goods + ".png"}
              alt=" "
            />
            <div className="small">
              <a href="#">
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m1.png"} alt=" " />
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m2.png"} alt=" " />
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m3.png"} alt=" " />
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m4.png"} alt=" " />
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m5.png"} alt=" " />
                <img src={process.env.PUBLIC_URL + "/images/goods/men/m6.png"} alt=" " />
              </a>
            </div>
          </section>
          <section className="gdesc scbar">
            <h1>HOME &gt; MEN</h1>
            <div>
              <ol>
                <li>
                  <img src={process.env.PUBLIC_URL + "/images/dx_ico_new-28143800.gif"} alt="new" />
                </li>
                <li id="gtit">: {ginfo[1]}</li>
                <li>
                  <img
                    src={process.env.PUBLIC_URL + "/images/icon_type02_social01.gif"}
                    alt=""
                  />
                  <img src={process.env.PUBLIC_URL + "/images/icon_type02_social02.gif"} alt="" />
                  <img src={process.env.PUBLIC_URL + "/images/icon_mail02.gif"} alt="" />
                  <img src={process.env.PUBLIC_URL + "/images/btn_source_copy.gif"} alt="URL" />
                </li>
                <li>
                  <span></span>
                  <span id="gprice">{addComma(ginfo[3])}</span>
                </li>
                <li>
                  <span></span>
                  <span>
                    <img src={process.env.PUBLIC_URL + "/images/icon_my_m02.gif"} alt="" />
                    4,950(5%)
                  </span>
                </li>
                <li>
                  <span></span>
                  <span>
                       
                    <img
                      src={process.env.PUBLIC_URL + "/images/view_btn_nointerest_card.gif"}
                      alt=""
                    />
                  </span>
                </li>
                <li>
                  <span></span> <span id="gcode">{ginfo[2]}</span>
                </li>
                <li>
                  <span></span> <span>95 100 105 110</span>
                </li>
                <li>
                  <span></span>
                  <span>
                    <input type="text" id="sum" defaultValue="1" />
                    <b className="chg_num">
                      <img src={process.env.PUBLIC_URL + "/images/cnt_up.png"} alt="" />
                      <img src={process.env.PUBLIC_URL + "/images/cnt_down.png"} alt="" />
                    </b>
                  </span>
                </li>
                <li>
                  <span></span> <span></span>
                </li>
                <li>
                  <span></span> <span></span>
                </li>
                <li className="tot">
                  <span></span>
                  <span id="total">{addComma(ginfo[3])}</span>
                </li>
              </ol>
            </div>
            <div>
              <button className="btn btn1">BUY NOW</button>
              <button className="btn" onClick={useCart}>
                SHOPPING CART
              </button>
              <button className="btn">WISH LIST</button>
            </div>
          </section>
        </div>
      </div>
    </>
  );
}

export default ItemDetail;
