// Pilot PJ  

//  API 
import { useContext } from "react";
import { pCon } from "./pCon";

export function TotalMenu() {
  //  
  const myCon = useContext(pCon);
  // pCon Provider value   / !

  //    ///
  const setPage = (catName) => {
    // catName  
    // (1)    :  
    myCon.setCatName(catName);
    // (2)    :  
    myCon.goPage("/fashion", { state: { catName: catName } });
    // (3)  
    document.querySelector(".ham").click();
  }; //////// setPage  ///////////

  //   //////////////////////
  return (
    <>
      <div className="mbox">
        <video
          src={process.env.PUBLIC_URL + "/images/disc2018.mp4"}
          loop="loop"
          muted="muted"
          className="bgm"
        ></video>
        <nav className="mlist">
          <dl>
            <dt>
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setPage("men");
                }}
              >
                MEN
              </a>
            </dt>
            <dd>
              <a href="#">T-SHIRT</a>
            </dd>
            <dd>
              <a href="#">JACKET</a>
            </dd>
            <dd>
              <a href="#">TRAINING WARE</a>
            </dd>
            <dd>
              <a href="#">BEACH WARE</a>
            </dd>
          </dl>
          <dl>
            <dt>
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setPage("women");
                }}
              >
                WOMEN
              </a>
            </dt>
            <dd>
              <a href="#">T-SHIRT</a>
            </dd>
            <dd>
              <a href="#">JACKET</a>
            </dd>
            <dd>
              <a href="#">TRAINING WARE</a>
            </dd>
            <dd>
              <a href="#">BEACH WARE</a>
            </dd>
          </dl>
          <dl>
            <dt>
              <a
                href="#"
                onClick={(e) => {
                  e.preventDefault();
                  setPage("style");
                }}
              >
                STYLE
              </a>
            </dt>
            <dd>
              <a href="#">COLLECTION</a>
            </dd>
            <dd>
              <a href="#">SEASON AD</a>
            </dd>
            <dd>
              <a href="#">STAR &amp; NEWS</a>
            </dd>
            <dd>
              <a href="#">MAIN ITEM</a>
            </dd>
          </dl>
          <dl>
            <dt style={{ paddingBottom: "2px" }}>
              <a
                href="#"
                onClick={(e) => {
                  // 
                  e.preventDefault();
                  // (1)   
                  myCon.setCatName('glist');
                  // (2)  
                  myCon.goPage('glist');
                  // (3)  
                  document.querySelector(".ham").click();
                }}
              >
                
              </a>
            </dt>
            <dd></dd>
          </dl>
        </nav>
      </div>
    </>
  );
} ///////// TotalMenu  //////////
