//   ////////////////

import React, { useContext, useEffect, useLayoutEffect, useRef } from "react";
import { pCon } from "./pCon";

//    /////
import { sinsangData } from "../../js/data/sinsang";

//   ////
import $ from "jquery";

function SinSang({ catName, chgItemFn, setPos }) {
  // 
  // (1) catName -  
  // (2) chgItemFn -  
  // (3) setPos -    

  //     ///
  // (left) ->   
  // ->  useRef !! -> .current !
  const lpos = useRef(0);
  //  (1-,0-)
  const callSts = useRef(1);

  //  cat    !
  useLayoutEffect(() => {
    //    
    lpos.current = 0;
    //  /  
    callSts.current = 1;
  }, [catName]); /////// catName 

  //  API
  const myCon = useContext(pCon);

  //  :   !
  const selData = sinsangData[catName];
  // console.log(selData);

  ///////////////////////////////////////////////////
  // [       ] /////
  ///////////////////////////////////////////////////
  const makeList = () => {
    //   
    let temp = [];
    //    for  JSX 
    for (let x = 0; x < 9; x++) {
      temp[x] = (
        <li
          className={"m" + (x + 1)}
          key={x}
          onMouseEnter={showInfo}
          onMouseLeave={removeInfo}
        >
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              chgItemFn("m" + (x + 1));
              // setTimeout(()=>{
              console.log("");
              let pos = $(".bgbx").offset().top - 95;

              //   top  !
              // setPos(pos);

              $("html,body").animate({ scrollTop: pos + "px" }, 500);
              // },100);
            }}
          >
            <img
              src={process.env.PUBLIC_URL + "/images/goods/" + catName + "/m" + (x + 1) + ".png"}
              alt=""
            />
          </a>
        </li>
      );
    } ///// for /////
    // JSX   ->!
    return temp;
  }; ///////// makeList  ///////////

  ////////////////////////////////////////////////
  // [      ] /////
  ////////////////////////////////////////////////
  const showInfo = (e) => {
    e.preventDefault();
    // 
    const tg = $(e.currentTarget);
    // 1.   li class( )
    let gKey = tg.attr("class");
    // console.log('!',selData[gKey]);

    // 2.   
    //   li 
    tg.append(`<div class="ibox"></div>`);

    // console.log(
    //   selData[gKey].split('^')
    //   .map((v)=>`<div>${v}</div>`));

    // 3. li  .ibox  +
    tg.find(".ibox")
      .html(
        selData[gKey]
          .split("^")
          .map((v, i) => `<div>${i == 2 ? addComma(v) + "" : v}</div>`)
      )
      // 
      .animate(
        {
          top: "110%",
          opacity: 1,
          zIndex: 1,
        },
        300
      );
  }; /////////// showInfo ///////////////

  //(   )
  function addComma(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  // [    ] ////
  const removeInfo = (e) => {
    e.preventDefault();
    $(e.currentTarget).find(".ibox").remove();
  };

  // [    ] //////
  const flowList = (ele) => {
    // ele-
    // console.log(ele);
    //  left 1 
    lpos.current--;

    //      
    if (lpos.current < -300) {
      //  !(-301 0 !)
      lpos.current = 0;
      //  li  
      ele.append(ele.find("li").first());
    } ///// if //////

    // 
    ele.css({ left: lpos.current + "px" });

    // 
    if (callSts.current) {
      // 0.04   (!)
      setTimeout(() => flowList(ele), 40);
    }
  }; ////////// flowList ////////////

  //      //////
  useEffect(() => {
    // : .flist
    //  !
    flowList($(".flist"));
  }, []); ////////// useEffect ////////

  //   ////////////////////
  return (
    <>
      <h2 className="c1tit">
        NEW {catName.toUpperCase()}'S ARRIVAL
        <button
        onClick={(e) => {
          e.preventDefault();
          //  
          myCon.setCatName('glist');
          //   
          myCon.goPage('/glist');
        }}
        ></button>
      </h2>
      <div
        className="flowbx"
        onMouseEnter={() => {
          //   !
          //   (0->false)
          callSts.current = 0;
        }}
        onMouseLeave={() => {
          //   !
          //   (1->true)
          callSts.current = 1;
          //   !
          flowList($(".flist"));
        }}
      >
        <ul className="flist">{makeList()}</ul>
      </div>
    </>
  );
}

export default SinSang;
